.reviews {
    @media (max-width: 768px) {
        display: none; }
    &__title {
        margin: 112px 0 10px 0; }
    &__description {
        max-width: 470px;
        margin: 0 auto 80px auto; }
    &__cards {
        display: flex;
        justify-content: space-between;
        grid-gap: 30px;
        margin-bottom: 112px; }
    &__card {
        width: 327px;
        min-height: 180px;
        padding: 30px 40px 30px 35px;
        &-text {
            font-weight: 600;
            font-size: 14px;
            line-height: 24px;
            letter-spacing: 0.2px;
            color: $grey;
            margin-bottom: 20px;
            max-width: 280px; }
        &-author {
            display: flex;
            &-avatar {
                margin-right: 15px; }
            &-description {
                display: flex;
                flex-direction: column; }
            &-name {
                font-weight: 600;
                font-size: 14px;
                line-height: 24px;
                letter-spacing: 0.2px;
                color: $blue; }
            &-job {
                font-weight: 600;
                font-size: 14px;
                line-height: 24px;
                letter-spacing: 0.2px; } } }
    &__rating {
        margin-bottom: 20px; } }
